

    
    .bar
    {
        width: 100%;
    height: 40px;
    color:blue;
        margin: 30px auto;
       
    }
    
    .bar ul
    {
    list-style: none;
       
    }
    .bar ul li
    {
    /* float: left; */
    list-style: none;
    display: inline-block;
        width: 100%;
        background-color: rgba(148, 148, 148,0.3);
        font-size: 20px;
        text-align: center;
        text-decoration: none;
        height: 40px;
        line-height: 40px;
        margin-right: 4px;
    }
    ul li a {
        display: block;
        cursor:context-menu;
    }
    ul li a:hover
    {
        display: block;
        background-color: floralwhite;
        color: steelblue;
    }
    .bar ul li ul li
    {
        margin-bottom: 0;
        display: none;
    }
    .bar ul li:hover ul li
    {
        display: block;
       
    }